import React from 'react';
import Image from 'next/image';
import Link from 'next/link';
import { formatDistanceToNow } from 'date-fns';
import { vi } from 'date-fns/locale';
import { User } from '@/types/user';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { useFollow } from '@/hooks/useFollow';

interface UserCardProps {
  user: User;
  showFollowButton?: boolean;
  showBio?: boolean;
  showStats?: boolean;
}

const UserCard: React.FC<UserCardProps> = ({
  user,
  showFollowButton = true,
  showBio = true,
  showStats = true,
}) => {
  const { isFollowing, followUser, unfollowUser, isLoading } = useFollow(user.id);

  const handleFollowToggle = async () => {
    if (isFollowing) {
      await unfollowUser();
    } else {
      await followUser();
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'ONLINE':
        return 'bg-green-500';
      case 'OFFLINE':
        return 'bg-gray-500';
      case 'AWAY':
        return 'bg-yellow-500';
      case 'BUSY':
        return 'bg-red-500';
      default:
        return 'bg-gray-500';
    }
  };

  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-4 flex items-start space-x-4">
      <div className="relative">
        <Link href={`/profile/${user.username}`}>
          <div className="relative w-16 h-16 rounded-full overflow-hidden">
            <Image
              src={user.avatar || '/image-person.png'}
              alt={user.username}
              fill
              className="object-cover"
            />
          </div>
        </Link>
        <div className={`absolute bottom-0 right-0 w-4 h-4 rounded-full border-2 border-white ${getStatusColor(user.status)}`} />
      </div>
      
      <div className="flex-1">
        <Link href={`/profile/${user.username}`}>
          <h3 className="font-semibold text-lg hover:text-blue-500 transition-colors">
            {user.fullName || user.username}
          </h3>
        </Link>
        <p className="text-gray-500 dark:text-gray-400 text-sm">@{user.username}</p>
        
        {showBio && user.bio && (
          <p className="text-gray-600 dark:text-gray-300 mt-1 text-sm line-clamp-2">
            {user.bio}
          </p>
        )}
        
        {showStats && (
          <div className="flex space-x-4 mt-2 text-sm text-gray-500 dark:text-gray-400">
            <div>
              <span className="font-semibold">{user.followersCount}</span> followers
            </div>
            <div>
              <span className="font-semibold">{user.followingCount}</span> following
            </div>
          </div>
        )}
        
        <div className="mt-2 text-xs text-gray-500 dark:text-gray-400">
          Last seen: {formatDistanceToNow(new Date(user.lastSeen), { addSuffix: true, locale: vi })}
        </div>
      </div>
      
      {showFollowButton && (
        <Button
          variant={isFollowing ? "outline" : "default"}
          size="sm"
          onClick={handleFollowToggle}
          disabled={isLoading}
          className="ml-auto"
        >
          {isLoading ? (
            <span className="flex items-center">
              <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              Processing...
            </span>
          ) : isFollowing ? (
            "Unfollow"
          ) : (
            "Follow"
          )}
        </Button>
      )}
    </div>
  );
};

export default UserCard; 