'use client';

import React, { useState, useEffect } from 'react';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { useAuth } from '@/hooks/useAuth';
import { api } from '@/lib/api';
import { User } from '@/types/user';
import UserCard from '@/components/friends/UserCard';
import { Search, Users, UserPlus, UserCheck } from 'lucide-react';
import { toast } from 'sonner';

const FriendsPage = () => {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState('followers');
  const [followers, setFollowers] = useState<User[]>([]);
  const [following, setFollowing] = useState<User[]>([]);
  const [suggestions, setSuggestions] = useState<User[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState({
    followers: true,
    following: true,
    suggestions: true,
  });

  // Fetch data based on active tab
  useEffect(() => {
    const fetchData = async () => {
      if (!user) return;

      setIsLoading(prev => ({ ...prev, [activeTab]: true }));

      try {
        let response;
        switch (activeTab) {
          case 'followers':
            response = await api.get('/follow/followers');
            setFollowers(response.data.data);
            break;
          case 'following':
            response = await api.get('/follow/following');
            setFollowing(response.data.data);
            break;
          case 'suggestions':
            response = await api.get('/follow/suggestions');
            setSuggestions(response.data.data);
            break;
        }
      } catch (error) {
        console.error(`Error fetching ${activeTab}:`, error);
        toast.error(`Failed to load ${activeTab}`);
      } finally {
        setIsLoading(prev => ({ ...prev, [activeTab]: false }));
      }
    };

    fetchData();
  }, [activeTab, user]);

  // Filter users based on search query
  const filterUsers = (users: User[]) => {
    if (!searchQuery) return users;
    
    const query = searchQuery.toLowerCase();
    return users.filter(user => 
      user.username.toLowerCase().includes(query) || 
      (user.fullName && user.fullName.toLowerCase().includes(query))
    );
  };

  // Render loading skeletons
  const renderSkeletons = () => {
    return Array(3).fill(0).map((_, index) => (
      <div key={index} className="bg-white dark:bg-gray-800 rounded-lg shadow-md p-4 flex items-start space-x-4">
        <Skeleton className="w-16 h-16 rounded-full" />
        <div className="flex-1 space-y-2">
          <Skeleton className="h-4 w-1/3" />
          <Skeleton className="h-3 w-1/4" />
          <Skeleton className="h-3 w-2/3" />
        </div>
      </div>
    ));
  };

  return (
    <div className="container mx-auto py-8 px-4">
      <h1 className="text-3xl font-bold mb-6">Friends</h1>
      
      <Tabs defaultValue="followers" className="w-full" onValueChange={setActiveTab}>
        <TabsList className="grid grid-cols-3 mb-6">
          <TabsTrigger value="followers" className="flex items-center gap-2">
            <Users className="h-4 w-4" />
            Followers
          </TabsTrigger>
          <TabsTrigger value="following" className="flex items-center gap-2">
            <UserCheck className="h-4 w-4" />
            Following
          </TabsTrigger>
          <TabsTrigger value="suggestions" className="flex items-center gap-2">
            <UserPlus className="h-4 w-4" />
            Suggestions
          </TabsTrigger>
        </TabsList>
        
        <div className="relative mb-6">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 h-4 w-4" />
          <Input
            placeholder="Search users..."
            className="pl-10"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
          />
        </div>
        
        <TabsContent value="followers">
          <Card>
            <CardHeader>
              <CardTitle>Your Followers</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {isLoading.followers ? (
                renderSkeletons()
              ) : filterUsers(followers).length > 0 ? (
                filterUsers(followers).map((user) => (
                  <UserCard key={user.id} user={user} />
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  {searchQuery ? 'No followers found matching your search' : 'You have no followers yet'}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="following">
          <Card>
            <CardHeader>
              <CardTitle>People You Follow</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {isLoading.following ? (
                renderSkeletons()
              ) : filterUsers(following).length > 0 ? (
                filterUsers(following).map((user) => (
                  <UserCard key={user.id} user={user} />
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  {searchQuery ? 'No following found matching your search' : 'You are not following anyone yet'}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
        
        <TabsContent value="suggestions">
          <Card>
            <CardHeader>
              <CardTitle>Suggested Users</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {isLoading.suggestions ? (
                renderSkeletons()
              ) : filterUsers(suggestions).length > 0 ? (
                filterUsers(suggestions).map((user) => (
                  <UserCard key={user.id} user={user} />
                ))
              ) : (
                <div className="text-center py-8 text-gray-500">
                  {searchQuery ? 'No suggestions found matching your search' : 'No suggestions available at the moment'}
                </div>
              )}
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
};

export default FriendsPage;