import { useState, useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { api } from '@/lib/api';
import { toast } from 'sonner';

export const useFollow = (userId: string) => {
  const [isFollowing, setIsFollowing] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const { user } = useAuth();

  // Kiểm tra trạng thái follow khi component mount
  useEffect(() => {
    const checkFollowStatus = async () => {
      if (!user) return;
      
      try {
        const response = await api.get(`/follow/status/${userId}`);
        setIsFollowing(response.data.data.isFollowing);
      } catch (error) {
        console.error('Error checking follow status:', error);
      }
    };

    checkFollowStatus();
  }, [userId, user]);

  // Follow một người dùng
  const followUser = async () => {
    if (!user) {
      toast.error('You must be logged in to follow users');
      return;
    }

    setIsLoading(true);
    try {
      await api.post(`/follow/follow/${userId}`);
      setIsFollowing(true);
      toast.success('Successfully followed user');
    } catch (error) {
      console.error('Error following user:', error);
      toast.error('Failed to follow user');
    } finally {
      setIsLoading(false);
    }
  };

  // Unfollow một người dùng
  const unfollowUser = async () => {
    if (!user) {
      toast.error('You must be logged in to unfollow users');
      return;
    }

    setIsLoading(true);
    try {
      await api.delete(`/follow/unfollow/${userId}`);
      setIsFollowing(false);
      toast.success('Successfully unfollowed user');
    } catch (error) {
      console.error('Error unfollowing user:', error);
      toast.error('Failed to unfollow user');
    } finally {
      setIsLoading(false);
    }
  };

  return {
    isFollowing,
    isLoading,
    followUser,
    unfollowUser,
  };
}; 